<template>
  <div>
    <mrc_ess_table v-if="$route.name=='staffLeave'"
                   :headers='headers'
                   pageName='addStaffLeave'
                   :paginationOptions='options'
                   @turnPage='getlist'
                   :isLoading='loading'
                   :slotKey="['name','result_status']"
                   :items='desserts'>

      <template v-slot:top>
        <v-card-title class="cardtitle"
                      style="height:60px">
          <mrc_ess_searchInput :isShow='true'
                               style="position:absolute;right:10px;top:18px;"></mrc_ess_searchInput>
          <span class="emailbtn">
            <mrc_ess_uploadBtn @sendRequest='sendRequest'
                               style="position:absolute;left:-100px"
                               :fn='fn'
                               :label="$t('label_Batch_Import')"
                               @findError='findError'
                               :uploadLoading='uploadLoading'
                               :resStatus='resStatus'>
            </mrc_ess_uploadBtn>
            <v-btn rounded
                   class="ml-3"
                   @click="dialog=true"
                   color="primary"
                   dark>{{$t('label_Download_the_template')}}</v-btn>

          </span>
        </v-card-title>
      </template>

      <template v-slot:item.name='{item}'>
        <mrc_ess_staffSpan :list='item'
                           class="mt-3 mb-3"></mrc_ess_staffSpan>
      </template>

      <!-- 状态 -->
      <template v-slot:item.result_status='{item}'>
        <v-btn rounded
               color="#999999"
               dark
               x-small
               v-if="item.result_status==''">{{$t('label_Unchanged')}}</v-btn>
        <v-btn rounded
               color="#13a671"
               dark
               x-small
               v-if="item.result_status==1">{{$t('label_Complete')}}</v-btn>
        <v-btn rounded
               color="#13a671"
               dark
               x-small
               v-if="item.result_status==2">{{$t('label_The_task_processing_is_complete')}}</v-btn>
      </template>

      <!-- 操作 -->
      <template v-slot:item.actions='{item}'>
        <menu-list :list='menulist'
                   @handleClick='handleClick($event,item)'>
          <template v-slot:showAction>
            <v-icon>mdi-dots-vertical</v-icon>
          </template>
        </menu-list>
      </template>
    </mrc_ess_table>
    <router-view v-else></router-view>
  </div>
</template>

<script>
import {
  getStaffLeaveList
  , addStaffLeave
  , updateStaffLeave
  , deleteStaffLeave
  , detailStaffLeave
} from '@/service/api/staffmsg.js'
export default {
  components: {

  },
  props: {},
  data () {
    return {
      fn: null,
      tableStatus: false,
      resStatus: null,
      disabled: true,
      list: {},
      options: {
        pagesize: 5,//条数
        cpage: this.$store.state.cpage,  //页数
        totalCount: null, //总条数
        totalpage: null, //总页数
      },
      loading: false,
      uploadLoading: false,
      desserts: [],
    };
  },
  computed: {
    menulist () {
      return [
        { name: this.$t('label_View') },
        { name: this.$t('label_Edit') },
        { name: this.$t('label_delete') },
      ]
    },
    headers () {
      return [
        { text: this.$t('label_Transfer_personnel'), align: 'start', value: 'name', sortable: false },
        { text: this.$t('label_Reason_for_separation'), value: 'dimission_reason', sortable: false },
        { text: this.$t('label_The_effective_date'), value: 'change_effective', sortable: false, },
        { text: this.$t('label_The_execution_status'), value: 'result_status', sortable: false, },
        { text: '', value: 'actions', sortable: false, align: 'end', },
      ]
    },
  },
  created () { },
  watch: {
    $route (v) {
      if (v.name == 'staffLeave') {
        this.getlist()
      }
    }
  },
  mounted () {
    this.getlist()
  },
  beforeDestroy () {
    this.$store.commit('setCpage', 1)
  },
  methods: {
    findError () {
      this.fn = getStaffLeaveList
    },
    sendRequest (val) {      //发送请求上传文件
      // console.log(val);
      this.uploadLoading = true
      setTimeout(() => {
        this.uploadLoading = false
        this.resStatus = false
      }, 2000)
    },
    async getlist (obj) {
      this.loading = true
      if (this.desserts.length == 1 && this.options.cpage != 1) this.options.cpage--
      if (obj) {
        const { cpage, pagesize } = obj
        this.options = obj
      }
      const res = await getStaffLeaveList({
        cpage: this.options.cpage, pagesize: this.options.pagesize
      })
      if (res.code == 200) {
        this.desserts = res.data
        this.options.totalCount = res.totalCount
        this.options.totalpage = res.totalpage
      }
      this.loading = false
    },
    addStaffLeave () {
      this.$store.commit('set_subPage_name', '')
      this.$store.commit('set_uuid', '')
      this.$store.commit('set_iconstatus', '')  //行内进图标显示
      this.$router.push({
        name: 'addStaffLeave'
      })
    },
    godetail (item) {
      this.$store.commit('set_iconstatus', true)  //行内进图标显示
      // this.$store.commit('set_subPage_name', item.job_name)
      this.$store.commit('set_uuid', item.uuid)  //
      this.$router.push({
        name: 'detailStaffLeave'
      })
    },
    handleClick ({ index }, item) {
      switch (index) {
        case 0:
          this.godetail(item)
          break
        case 1:
          // this.$store.commit('set_subPage_name', item.job_name)
          this.$store.commit('set_iconstatus', false)  //行内进图标显示 
          this.$store.commit('set_uuid', item.uuid)  //
          this.$router.push({
            name: 'detailStaffLeave'
          })
          break
        case 2:
          this.$myConfirm({ content: this.$t('message_Confirm_the_deletion') }).then(res => {
            deleteStaffLeave({ uuid: item.uuid }).then(res => {
              if (res.code === '200') {
                this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
                if (this.desserts.length = 1) this.options.cpage--
                this.getlist()
              }
            })
          })
          break
      }
    },
    success () { },
    cancel () { }
  },
};
</script>

<style scoped>
.cardtitle {
  position: relative;
}
.emailbtn {
  position: absolute;
  right: 70px;
  top: -50px;
}
.dowanTemplate {
  position: absolute;
  right: 180px;
  top: -50px;
}
</style>
